body {
  color:#052F5F;
  font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
  -webkit-text-stroke-color: #FDCE38;
  -webkit-text-stroke-width: 0.5px;
}

a {
  color: #052F5F;
  text-shadow:0 0 10px #FDCE38;
}

a:hover {
  color: #FDCE38;
  text-decoration: underline;
  text-shadow:0 0 17px #FDCE38;
  -webkit-transition: 1s;
  -o-transition: 1s;
  transition: 1s;
}

form h1{
  -ms-flex-line-pack: center;
      align-content: center;
  color: #052F5F;
  font-size: 1.8em;
  font-weight: bolder;
  margin-top: 40px;
}

form h3{
  font-weight: bolder;
  margin: 5px;
}

html{
  background-color: #090446
}

input {
  margin: 5px;
  margin-top: 10px;
}

input[type="submit"] {
  margin: 0px;
}

li {
  font-size: 1rem;
  list-style-type: none;
  margin-bottom: 0.5rem; 
}

li a {
  color: #FDCE38;
  display: block;
  padding: 14px 16px;
  text-align: center;
  text-decoration: none;
}

ul {
  list-style-type: disc;
}


/* ============================================================== */


#welcome{
  font-size: 2.5em;
  position: absolute;
  text-align: center;
  top:20px;
  -webkit-transform: rotate(-12deg);
      -ms-transform: rotate(-12deg);
          transform: rotate(-12deg);
  -webkit-text-stroke-color: #FDCE38;
  -webkit-text-stroke-width: 0.33px;
}


/* ============================================================== */


.editModal{
  margin: 0 auto;
}

.form {
  -webkit-animation: fadein 3s;
          animation: fadein 3s;
  background-color: #FDCE38;
  border-radius: 175px;
  -webkit-box-shadow:0 0 100px #FDCE38;
          box-shadow:0 0 100px #FDCE38;
  color: #052F5F;
  font-size: 1.125em;
  height: 325px;
  margin: 0 auto;
  margin-top: 30px;
  overflow:hidden;
  padding-top: 10px;
  text-align: center;
  width: 325px;
}

.formDiv{
  padding: 0;
  text-align: center;
}


.historyCard{
  -webkit-animation: fadein 1.25s;
          animation: fadein 1.25s;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
}

.infoSection li:before {
  content: "\263C";
  font-size: 2rem;
  padding-right: 12px;
}

.instructions h1 {
  font-size: 9em;
  font-weight: 700;
}

.instructions {
  background-color: #00acb0;
  color: #052F5F;
  padding: 0px;
}

.locationModal{
  -ms-flex-align: center;
      align-items: center;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  -ms-flex-direction: column;
      flex-direction: column;
}

.loginForm{
  position: relative;
}

.message {
  color: #0072BB;
  padding: 10px 0;
}

.message p {
  color: #0072BB;
  margin-left: 10px;
}

.message:nth-child(odd) {
  background-color: #eeeeee;
}

.message:nth-child(even) {
  background-color: #f8f8f8;
}

.navbar {
  background-color: #052F5F;
  border-radius: 0px;
  width: 100%;
}

/* ===================================== */

@-webkit-keyframes fadein {
  from { opacity: 0; }
  to   { opacity: 1; }
}

@keyframes fadein {
  from { opacity: 0; }
  to   { opacity: 1; }
}



